
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { mavonEditor } from 'mavon-editor';
import 'mavon-editor/dist/css/index.css';
export default {
name: "edit",
props: ["id"],
data: function() {
return {
topic: {
title: "",
content: "",

},
toolbars: {
bold: true, // 
italic: true, // 
header: true, // 
underline: true, // 
ol: true, // 
ul: true, // 
link: true, // 
imagelink: true, // 
code: true, // code
fullscreen: true, // 
htmlcode: true, // html
help: true, // 
/* 2.1.8 */
alignleft: true, // 
aligncenter: true, // 
alignright: true, // 
/* 2.2.1 */
subfield: true, // 
preview: true, // 
}
};
},
methods: {

edittopic: function() {
var app = this;
this.$http.post('/topics/update', {
topic_id: this.topic.id,
title: this.topic.title,
content: this.topic.content,
accesstoken: this.$store.state.accesstoken,
tab: 'dev'
}).then(function(response){


app.$router.back();
})
}
},
components: {
Group,
XTextarea,
XButton,
mavonEditor
},
beforeRouteEnter: function(to, from, next) {
next(function(vm) {
vm.$http.get("/topic/" + to.params.id).then(function(response) {
vm.topic = response.data.data;
});
});
},
beforeRouteUpdata: function (to, from, next) {
var app = this;
app.$http.get('/topic/' + to.params.id).then(function (response) {
app.topic = response.data.data;
});
next();
}
};



